import Vue from 'vue'
import Vuex from 'vuex'
import api from './services/api'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        todos: [],
        addTodo: {
            visible: false,
            todo: null,
            mode: 'add'
        }
    },
    getters: {
        todoList: state => {
            return state.todos
        }
    },
    mutations: {
        SHOW_DIALOG(state, payload) {
            state.addTodo = {
                visible: payload.visible,
                mode: payload.mode,
                todo: payload.todo
            }
        },
        SET_TODOS(state, payload) {
            state.todos = payload;
        },
        ADD_TODO(state, payload) {
            state.todos.push(payload);
        },
        DELETE_TODO(state, payload) {
            var item = state.todos.find(todo => todo.id == payload)
            state.todos.splice(state.todos.indexOf(item), 1);
        },
        UPDATE_TODO(state, payload) {
            var index = state.todos.indexOf(payload);
            state.todos[index] = payload
        }
    },
    actions: {
        fetchTodos: async ({ commit }) => {
            api().get(`${process.env.VUE_APP_URL}/all`)
                .then((res) => commit('SET_TODOS', res.data))
        },
        addItem({ commit }, todo) {
            api().post(`${process.env.VUE_APP_URL}/create`, todo)
                .then((res) => commit('ADD_TODO', res.data))
                .catch(() => {
                    console.log("Could not add todo")
                })
        },
        deleteTodo({ commit }, id) {
            api().delete(`${process.env.VUE_APP_URL}/delete/${id}`)
                .then(() => commit('DELETE_TODO', id))
                .catch(() => {
                    console.log("Could not delete")
                })
        },
        updateTodo({ commit }, todo) {
            api().put(`${process.env.VUE_APP_URL}/update/${todo.id}`,todo)
                .then(() => commit('UPDATE_TODO', todo))
                .catch((err) => {
                 console.log(err)
             })
        }
    }
    })

export default store;