<template>
  <v-container my-10>
      <v-row justify="center" class="" align="center">
    
    <v-col cols="12" lg="5" md="5"> 
    <add-todo></add-todo>
    <v-layout
    my-1
    mb-5
    align-center>
    <strong class="info-text text--darken-3">
        Progress ({{`${completed}/${todoList.length}`}})
    </strong>
    <v-spacer></v-spacer>
    <v-spacer></v-spacer>
        <v-progress-circular
            :value="progress"
            class="mr-2"
            size="20"
            :color="color"
          ></v-progress-circular>
    </v-layout>
    <v-card v-if="todoList.length > 0">
        <v-slide-y-transition
          class="py-0"
          group
          tag="v-list"
        > 
         <template   
         v-for="(item,index) in todoList"
        >
        
         <v-list-item
        :key="item.id">
         <v-list-item-action>
              <v-checkbox 
              v-model="toggle[index]"
              ></v-checkbox>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title><span :class="{completed:item.completed,'grey--text':item.completed}">{{item.todo}}</span></v-list-item-title>
              <v-list-item-subtitle>{{item.created_at | moment('ddd MMM d')}}</v-list-item-subtitle>
            </v-list-item-content>
            <v-list-item-icon>
                <v-btn v-show="item.completed && !toggle[index]" icon small>
                    <v-icon color="success">mdi-check</v-icon>
                  </v-btn>
              <v-scroll-y-transition>
               <v-btn-toggle
                v-show="toggle[index]"
                borderless
                >
                  <v-btn icon small @click="complete(item.id)">
                    <v-icon>{{item.completed ? 'mdi-close' : 'mdi-check'}}</v-icon>
                  </v-btn>
              <v-btn icon small @click="deleteTodo(item.id,index)">
            <v-icon color="error">mdi-delete</v-icon>
          </v-btn>
             </v-btn-toggle>
              </v-scroll-y-transition>
            </v-list-item-icon>
         </v-list-item>
            <v-divider
            v-if="index < todoList.length - 1"
            :key="index"
          ></v-divider>
         </template>
      </v-slide-y-transition> 
    </v-card> 
      <div v-else >
         No item items found
    </div>
    </v-col>

      </v-row>
  </v-container>
</template>

<script>
import {mapGetters} from 'vuex';
import AddTodo from './AddTodo.vue';

export default {
  data:() => ({
     toggle:[],
     loading:true
  }),
  components: { AddTodo },
  computed:{
    ...mapGetters(['todoList']),
    completed(){
        return this.todoList.filter(todo => todo.completed).length
    },
        incomplete(){
        return this.todoList.filter(todo => !todo.completed).length
    },
    progress(){
        return this.completed/this.todoList.length * 100
    },
    color(){
        return this.progress == 100 ? 'success' : 'info'
    }
  },
  methods:{
      deleteTodo(id,index){
        this.$store.dispatch('deleteTodo', id)
        .then(this.toggle[index] = false)
      },
      complete(id){
          var item = this.todoList.find(todo=> todo.id == id);
          item.completed = !item.completed;
          this.$store.dispatch('updateTodo', item)
      }
  },
     mounted(){
    this.$store.dispatch('fetchTodos');
  }
}
</script>

<style>
.completed{
      text-decoration: line-through;
}
</style>