<template>
            <v-text-field
            label="What do you want to do?"
            solo
            flat
            v-model="title"
            background-color="grey lighten-2"
            class="rounded-lg"
            @keypress.enter.prevent="submit"
            >
         <v-fade-transition slot="append">
        <v-btn 
        @click="submit" 
        :loading="loading" 
        v-if="title"
        icon
       >
        <v-icon>
            mdi-plus-circle
            </v-icon></v-btn>
         </v-fade-transition>
            </v-text-field>
     
</template>

<script>
export default {
data:() => ({
valid:true,
loading:false,
     title:'',
     id:null,
     completed:false,
 rule:[
     v => !!v ||'This field is required'
 ]
}),
methods:{
    submit(){
     if(this.valid){
       this.$store.dispatch('addItem',{
           todo:this.title,
           completed:this.completed,
           mode:'add'
       })
       .then(()=>{
          this.title="";
          this.completed=false;
          
       })
     }
    },
},
}
</script>

